.list__item{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 24px;
    border: 1px solid #6F6F6F;
    padding: 25px;
    margin-bottom: 10px;
    
  }

  @media screen and (max-width: 500px) {
    .list__item{
      font-size: 18px;
      padding: 10px;
    }
  }

  @media screen and (max-width: 380px) {
    .list__item{
      font-size: 16px;
    }
  }
  
  .list__item-label{
    white-space: normal;
    display: flex;
    justify-content: space-between;
    flex-shrink: unset;
    align-items: center;
  }
  
  .list__item-buttons{
    display: flex;
    flex-wrap: nowrap;
  }
  
  .list__item-button{
    display: inline-block;
    margin: 0;
    padding: 0;
    margin-left: 20px;
    box-sizing: border-box;
    width: 22px;
    height: 22px;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }

  .list__item-button_edit{
    background-image: url('../../images/icons/EditIcon.svg');
    background-size: cover;
  }

  .list__item-button_copy{
    background-image: url('../../images/icons/CopyIcon.svg');
    background-size: cover;
  }

  .list__item-button_delete{
    background-image: url('../../images/icons/DeleteIcon.svg');
    background-size: cover;
  }

  @media screen and (max-width: 500px) {
    .list__item-button{
      width: 18px;
      height: 18px;
    }
  }
  
  .list__item-text{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
  }
  
  .list__item-checkbox{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    margin-right: 10px;
    width: 20px;
    height: 20px;
    border: none;
  }
  

  .label-strike{
    text-decoration: line-through;
  }
  