@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  width: 100%;
  margin: 0;
  background-color: black;
  color: #ffffff;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  width: 80%;
  box-sizing: border-box;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 650px) {
  .App{
    width: 100%;
    padding: 0 20px 0 20px;
  }
}

.filter__buttons{
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
  text-align: right;
}

.filter__button{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  padding: 5px 10px 5px 10px;
  font-size: 18px;
  border: none;
  background-color: transparent;
  color: #ffffff;
  cursor: pointer;
  text-align: right;
}

.filter__button:hover{
  opacity: 0.7;
  transition: all 0.6s ease-in;
}

.list{
  margin: 0;
  padding: 0;
  width: 70%;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media screen and (max-width: 1000px) {
  .list{
    width: 80%;
  }
}

@media screen and (max-width: 850px) {
  .list{
    width: 90%;
  }
}

@media screen and (max-width: 650px) {
  .list{
    width: 100%;
  }
}




.header{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: fixed;
    top: 0;
    background-color: black;
  }
  
  .header__nav{
    display: flex;
    flex-direction: column;
    justify-items: flex-end;
  }

  @media screen and (max-width: 500px) {
    .header__nav{
      font-size: 16px;
    }
}
  
  .header__heading{
    width: 100%;
    margin: 0;
    padding: 0;
    margin: 0 0 20px 0;
    text-align: center;
  }


.footer{
    margin: 30px 0 30px 0;
    color: #6F6F6F;
  }
.form{
    width: 70%;
    margin: 0;
    padding: 0;
    margin-top: 120px;
    margin-bottom: 30px;
    box-sizing: border-box;
    display: flex;
  }

@media screen and (max-width: 1000px) {
    .form{
      width: 80%;
    }
}

@media screen and (max-width: 850px) {
  .form{
    width: 90%;
  }
}

@media screen and (max-width: 650px) {
  .form{
    width: 100%;
  }
}
  
  .form__input{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    height: 100%;
    width: 75%;
    font-size: 24px;
    padding: 10px;
    border-radius: 5px;
  }

  @media screen and (max-width: 500px) {
    .form__input{
      font-size: 20px;
      width: 65%;
    }
  }

  
  .form__button{
    display: inline-block;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    padding: 10px;
    margin-left: 10px;
    height: 100%;
    width: 25%;
    border: none;
    background-color: #ffffff;
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    border-radius: 5px;
    cursor: pointer;
  }

  @media screen and (max-width: 500px) {
    .form__button{
      font-size: 20px;
      width: 35%;
    }
  }
.list__item{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 24px;
    border: 1px solid #6F6F6F;
    padding: 25px;
    margin-bottom: 10px;
    
  }

  @media screen and (max-width: 500px) {
    .list__item{
      font-size: 18px;
      padding: 10px;
    }
  }

  @media screen and (max-width: 380px) {
    .list__item{
      font-size: 16px;
    }
  }
  
  .list__item-label{
    white-space: normal;
    display: flex;
    justify-content: space-between;
    flex-shrink: unset;
    align-items: center;
  }
  
  .list__item-buttons{
    display: flex;
    flex-wrap: nowrap;
  }
  
  .list__item-button{
    display: inline-block;
    margin: 0;
    padding: 0;
    margin-left: 20px;
    box-sizing: border-box;
    width: 22px;
    height: 22px;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }

  .list__item-button_edit{
    background-image: url(../../static/media/EditIcon.d0e39b4b.svg);
    background-size: cover;
  }

  .list__item-button_copy{
    background-image: url(../../static/media/CopyIcon.7d1f6ed1.svg);
    background-size: cover;
  }

  .list__item-button_delete{
    background-image: url(../../static/media/DeleteIcon.c632a4c4.svg);
    background-size: cover;
  }

  @media screen and (max-width: 500px) {
    .list__item-button{
      width: 18px;
      height: 18px;
    }
  }
  
  .list__item-text{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
  }
  
  .list__item-checkbox{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    margin-right: 10px;
    width: 20px;
    height: 20px;
    border: none;
  }
  

  .label-strike{
    text-decoration: line-through;
  }
  
.checkbox{
    margin: 0;
    padding: 0;
    display: block;
    box-sizing: border-box;
    margin-right: 5px;
    width: 22px;
    min-width: 22px;
    height: 22px;
    border: none;
    background-color: #ffffff;
    background-size: cover;
    border-radius: 25%;
}

.checkbox_checked{
    background-image: url(../../static/media/checkbox_active.2b3d018b.svg);
    background-size: cover;
}

@media screen and (max-width: 500px) {
    .checkbox{
      width: 18px;
      min-width: 18px;
      height: 18px;
    }
  }
