.form{
    width: 70%;
    margin: 0;
    padding: 0;
    margin-top: 120px;
    margin-bottom: 30px;
    box-sizing: border-box;
    display: flex;
  }

@media screen and (max-width: 1000px) {
    .form{
      width: 80%;
    }
}

@media screen and (max-width: 850px) {
  .form{
    width: 90%;
  }
}

@media screen and (max-width: 650px) {
  .form{
    width: 100%;
  }
}
  
  .form__input{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    height: 100%;
    width: 75%;
    font-size: 24px;
    padding: 10px;
    border-radius: 5px;
  }

  @media screen and (max-width: 500px) {
    .form__input{
      font-size: 20px;
      width: 65%;
    }
  }

  
  .form__button{
    display: inline-block;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    padding: 10px;
    margin-left: 10px;
    height: 100%;
    width: 25%;
    border: none;
    background-color: #ffffff;
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    border-radius: 5px;
    cursor: pointer;
  }

  @media screen and (max-width: 500px) {
    .form__button{
      font-size: 20px;
      width: 35%;
    }
  }